import { Component } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { IonicPage, LoadingController, AlertController, NavController, NavParams, MenuController } from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';
import { AuthProvider } from '../../providers/auth/auth';
// import { ConstantVariable } from "../../app/constant-variable";

// const SERVER_URL: any = {
//   getNormal: ConstantVariable.APIURL + 'auth/config/settings',
//   getImageUrl: ConstantVariable.IMAGEURL,
// };

@IonicPage()
@Component({
  selector: 'page-login-background-slider',
  templateUrl: 'auth.html'
})
export class AuthPage {

  backgrounds = [
    'assets/img/background/background-1.jpg',
    'assets/img/background/background-2.jpg',
    'assets/img/background/background-3.jpg',
    'assets/img/background/background-4.jpg'
  ];
  public loginForm: any;
  films: Observable<any>;
  settings: any;
  coop_details: any;
  data: any;
  loading: any;
  imageUrl: String;
  private login: FormGroup;
  loginData = { username: '', password: '' };

  constructor(public formBuilder: FormBuilder,
              public navCtrl: NavController,
              public navParams: NavParams,
              private menu: MenuController,
              public alertCtrl: AlertController,
              public httpClient: HttpClient,
              public loadingCtrl: LoadingController,
              public authProvider: AuthProvider) {
    this.loginForm = formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.compose([Validators.minLength(6),
      Validators.required])]
    });

    this.login = this.formBuilder.group({
      username: ['', Validators.required],
      password: [''],
    });
  }

  ionViewDidLoad() {
    console.log('Hello Auth Page');
  }

  ionViewDidEnter() {
    // the root left menu should be disabled on this page
    this.menu.enable(false);
  }

  ionViewWillLeave() {
    // enable the root left menu when leaving this page
    this.menu.enable(true);
  }

  logForm() {
    console.log(this.login.value)
  }

  openResetPassword() {
    console.log('Reset password clicked');
    this.navCtrl.push('ForgotPasswordPage');
  }

  presentLoading(message, title) {
    const loading = this.loadingCtrl.create({
      duration: 500
    });

    loading.onDidDismiss(() => {
      const alert = this.alertCtrl.create({
        title: title,
        subTitle: message,
        buttons: ['Dismiss']
      });
      alert.present();
    });

    loading.present();
  }




  // doLogin() {
  //   console.log(this.loginData)
  //   if (this.loginData.username.length < 1 || this.loginData.password.length < 1) {
  //     this.presentLoading('Username or Password cannot be empty', 'Error')
  //     // this.loading.dismiss();
  //   } else {

  //     this.showLoader();
  //     this.authProvider.login(this.loginData).then((result) => {
  //       this.loading.dismiss();
  //       this.data = result;
  //       this.authProvider.storeUser(this.data.user).then(storedUser => {
  //         // console.log('StoredUser :' + storedUser)
  //         this.navCtrl.push('Homev1Page');
  //       })
  //     }, (err) => {
  //       this.loading.dismiss();
  //       console.log(err)
  //       this.presentLoading('The credentials do not match our records', 'Error');
  //     });
  //   }
  // }


  showLoader() {
    this.loading = this.loadingCtrl.create({
      content: 'Authenticating...'
    });
    this.loading.present();
  }

  doLogin() {
    console.log(this.loginForm)
    console.log(this.loginData)
    console.log(this.loginForm.value)

    if (!this.loginForm.valid) {
      console.log('Invalid or empty dataAA');
    } else {
      console.log('Passed');
      const userEmail = this.loginForm.value.email;
      const userPassword = this.loginForm.value.password;

      console.log('user data', userEmail, userPassword);
    }
  }

}



